
import { defineComponent, onMounted, reactive } from 'vue'
import { useRouter } from "vue-router"

export default defineComponent({
  name: 'Search',
  components: {
  },
  setup(props, context){
    const state = reactive({
      value: "",
      searchList: [""]
    })
    const router = useRouter()

    // 数据初始化
    onMounted(() => {
      state.searchList = ["龙虾", "大闸蟹", "666666", "哈哈哈哈哈哈", "123", "summer", "zero"]
    })

    // 搜索
    const onSearch = () => {
      console.log("value0---", state.value)
      state.searchList.unshift(state.value)
    }

    // 清除记录
    const handleClearRecord = () => {
      state.searchList = []
    }

    // 点击删除
    const handleDelete = (item: string, index: number) => {
      state.searchList.splice(index, 1)
      console.log("xxx", item)
    }

    // 取消返回首页
    const onCancel = () => {
      router.push("/home")
    }

    // 点击搜索
    const handleClickSearch = (item: any) => {
      state.value = item
    }

    return {
      state,
      onSearch,
      handleClearRecord,
      handleDelete,
      onCancel,
      handleClickSearch
    }
  }
})
